.react-tabs {
  -webkit-tap-highlight-color: transparent;
  width: 100%;
  display: flex; }
  @media (max-width: 900px) {
    .react-tabs {
      display: block; } }
  .react-tabs .tabs-menu {
    padding: 0; }
  .react-tabs .tabs-content {
    padding: 0; }
  .react-tabs__tab-list {
    float: none;
    margin: 0;
    padding: 0;
    list-style: none;
    border: 0px;
    background-color: #3f51b5;
    width: 110px;
    z-index: 999;
    text-align: center;
    margin: 0; }
    @media (max-width: 900px) {
      .react-tabs__tab-list {
        width: 100%;
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        align-content: space-between;
        justify-content: center;
        margin: 0 auto 30px auto; } }
  .react-tabs__tab {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 80px;
    border-bottom: 1px solid white;
    color: #fff; }
    @media (max-width: 900px) {
      .react-tabs__tab {
        width: 120px;
        border: none; } }
    .react-tabs__tab--selected {
      background: #fff;
      color: #3f51b5;
      font-weight: bold; }
      @media (max-width: 900px) {
        .react-tabs__tab--selected {
          background: #3f51b5;
          color: #fff; } }
    .react-tabs__tab--disabled {
      color: red;
      cursor: default; }
    .react-tabs__tab:focus {
      outline: none; }
      .react-tabs__tab:focus:after {
        content: "";
        position: absolute;
        height: 5px;
        left: -4px;
        right: -4px;
        bottom: -5px;
        background: #fff; }
    .react-tabs__tab:hover {
      background: #ffffff24;
      color: #ededed; }
    .react-tabs__tab p {
      margin: 0;
      cursor: pointer; }
  .react-tabs__tab-panel {
    display: none;
    text-align: left; }
    .react-tabs__tab-panel--selected {
      display: block; }

