.about-container .paper-background {
  display: block;
  position: relative;
  width: 860px;
  height: auto;
  padding: 70px 0;
  margin: 30px auto;
  color: #333;
  background-color: #f9f9f9;
  font-family: 'Nothing You Could Do', cursive;
