    bottom: 0;
    left: 96px;
    z-index: 3;
    width: 4px;
    height: 100%;
    border-left: 4px double #fca1a1; }
  .about-container .paper-background:after {
    content: " ";
    display: inline-block;
    position: absolute;
    top: 0;
    bottom: 0;
    right: 96px;
    z-index: 4;
    width: 4px;
    height: 100%;
    border-left: 1px solid #fca1a1; }
  .about-container .paper-background .coffee-stain {
