  .about-container .paper-background .paper-date {
    position: absolute;
    z-index: 5;
    width: 300px;
    background-color: transparent;
    font-size: 20px;
    border: none;
    vertical-align: baseline;
    right: 120px;
    z-index: 5;
    text-align: right; }
    .about-container .paper-background .paper-date:focus {
      outline: none; }
  .about-container .paper-background .paper-content {
    position: relative;
    top: 0;
    left: 0;
    z-index: 2;
    padding: 76px 106px;
    height: 100%;
    background: repeating-linear-gradient(to bottom, #f9f9f9, #f9f9f9 23px, #6ba2d8 1px, #f9f9f9);
    background-size: 100% 24px;
    line-height: 24px; }
    .about-container .paper-background .paper-content:before, .about-container .paper-background .paper-content:after {
      box-shadow: inset 0px 2px 5px 0px rgba(0, 0, 0, 0.26); }
    .about-container .paper-background .paper-content:before {
      content: " ";
      display: inline-block;
      position: absolute;
      top: 28%;
      left: 34px;
      z-index: 3;
      width: 24px;
      height: 24px;
      background-color: #ededed;
      border-radius: 50%; }
    .about-container .paper-background .paper-content:after {
      content: " ";
      display: inline-block;
      position: absolute;
      top: 58%;
